import { hover } from "./hover";
export default class Player {
  constructor(container) {
    this.loadingBar = null; // loading 进程
    this.playerProcessBar = null; // 播放进度

    this.video = document.createElement("video");
    this.video.setAttribute("x5-video-player-type", "h5");
    // playsinline  webkit-playsinline
    this.video.setAttribute("playsinline", true);
    this.video.setAttribute("webkit-playsinline", true);
    this.container = container;
    this.container.style = "position:relative";
    this.createPlayStyle();
    // 创建video 控制器
    this.createControl();
    // video 事件
    this.initVideoEvent();
    return this;
  }
  createPlayStyle() {
    const playBtn = document.createElement("div");
    playBtn.style = `width:50px;height:50px;position:absolute;left:50%;top:50%;background:red;transform: translate(-50%, -50%);`;
    this.container.appendChild(playBtn);
    playBtn.onclick = () => {
      this.video.play();
      this.video.muted = false;
    };
  }

  createControl() {
    const playBtnBox = document.createElement("div");
    playBtnBox.style = `
    width:100%;
    height:60px;
    position:absolute;
    left:0;
    bottom:0;
    overflow: visible !important;`;
    this.container.appendChild(playBtnBox);
    const controlBox = document.createElement("div");
    controlBox.style = `
      color: #fff;
      position: relative;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,16,27,0.7);
      background-clip: content-box;
    `;
    playBtnBox.appendChild(controlBox);
    this.createProcessBar(playBtnBox);
  }
  // 创建进度条
  createProcessBar(playBtnBox) {
    const div = document.createElement("div");
    this.processBox = div;
    div.style = `
      width:100%;
      position: absolute;
      height: 6px;
      bottom: 100%;
      top:0;
      cursor: pointer;
      z-index: 2;
      transition: transform .1s;`;
    playBtnBox.appendChild(div);

    const processBar = document.createElement("div");
    processBar.style = `
        width:100%;
        position: absolute;
        height: 6px;
        bottom: 100%;
        cursor: pointer;
        background-color: rgba(255,255,255,0.35);
      `;
    div.appendChild(processBar);
    // 加载进度
    this.loadingBar = document.createElement("div");
    this.loadingBar.style = `
      transition: all .1s;
      width:0%;
      position: absolute;
      height: 6px;
      bottom: 100%;
      background-color: rgba(255,255,255,0.35);
    `;
    div.append(this.loadingBar);
    // 播放进度
    this.playerProcessBar = document.createElement("div");
    this.playerProcessBar.style = `
    transition: all .1s;
      width:0%;
      position: absolute;
      height: 6px;
      bottom: 100%;
      background-color: rgba(102,153,0,1);
    `;
    div.append(this.playerProcessBar);
    this.createPlayCicle(this.playerProcessBar);
  }
  createPlayCicle(playerProcessBar) {
    const cricle = document.createElement("div");
    cricle.style = `
      opacity:0;
      transition: all .1s;
      width:12px;
      height:12px;
      position: absolute;
      right: 0px;
      top:50%;
      background-color: #fff;
      border-radius:50%;
      transform:translateY(-50%);
    `;
    playerProcessBar.append(cricle);
    hover(
      this.processBox,
      () => {
        cricle.style.opacity = 1;
      },
      () => {
        cricle.style.opacity = 0;
      }
    );
  }
  setSrc(src) {
    const video = this.video;
    video.autoplay = true;
    video.muted = true;
    video.loop = true;
    video.setAttribute("crossOrigin", "Anonymous");
    this.video.src = src;
  }

  initVideoEvent() {
    const that = this;
    this.video.addEventListener(
      "timeupdate",
      function (e) {
        const duration = Math.ceil(this.duration);
        var timeDisplay;
        //用秒数来显示当前播放进度
        timeDisplay = Math.floor(this.currentTime);
        that.playerProcessBar.style.width = `${
          (timeDisplay / duration) * 100
        }%`;
        that.loadProcess(e);
      },
      false
    );
  }
  //下载进度提示
  loadProcess(e) {
    let time = 0;
    if (parseInt(e.target.currentTime) !== Number(time)) {
      time = parseInt(e.target.currentTime);
      const timeRanges = this.video.buffered;

      // 获取已缓存的时间  timeRanges.end(timeRanges.length - 1)
      let p =
        parseInt(
          ((timeRanges.end(timeRanges.length - 1) * 100) /
            this.video.duration) *
            100
        ) / 100;
      this.loadingBar.style.width = `${p}%`;
      // console.log(p);
    }
  }
}
