import * as THREE from "three";
import CameraControls from "camera-controls";
import Player from "./player";
export default class VideoVr {
  constructor(container) {
    this.container = container;
    this.clock = new THREE.Clock();
    CameraControls.install({ THREE: THREE });
    this.videoPlayer = new Player(this.container);
    this.init();
  }
  // 初始化
  init() {
    const canvas = document.createElement("canvas");
    canvas.style = "width:100%;height:100%;postion:absolute;left:0;right:0";
    this.renderer = new THREE.WebGLRenderer({ canvas: canvas });
    this.renderer.setClearColor(0xaaaaaa);
    this.container.appendChild(canvas);
    this.camera = this.makeCamera();
    this.camera.position.set(0, 0, 0.1).multiplyScalar(1);
    this.scene = new THREE.Scene();
    this.createVidoBox(this.videoPlayer.video);
    this.cameraControls = new CameraControls(
      this.camera,
      this.renderer.domElement
    );
    this.render(this.renderer, this.camera, this.scene)();
  }
  createVidoBox(video) {
    var texture = new THREE.VideoTexture(video);
    var material = new THREE.MeshBasicMaterial();
    material.map = texture;

    this.skyBox = new THREE.Mesh(
      new THREE.SphereBufferGeometry(30, 30, 30),
      material
    );
    this.skyBox.position.set(0, 0, 0);
    this.skyBox.geometry.scale(-1, 1, 1);

    this.scene.add(this.skyBox);
  }
  makeCamera(fov = 40) {
    const aspect = 2;
    const near = 0.1;
    const far = 1000;
    return new THREE.PerspectiveCamera(fov, aspect, near, far);
  }
  resizeRendererToDisplaySize(renderer) {
    const canvas = renderer.domElement;
    const width = canvas.clientWidth;
    const height = canvas.clientHeight;
    const needResize = canvas.width !== width || canvas.height !== height;
    if (needResize) {
      renderer.setSize(width, height, false);
    }
    return needResize;
  }
  render(renderer, camera, scene) {
    const that = this;
    return function inner() {
      const delta = that.clock.getDelta();
      const hasControlsUpdated = that.cameraControls.update(delta);

      if (that.resizeRendererToDisplaySize(renderer)) {
        const canvas = renderer.domElement;
        // const camera = cameraInfo.cam;
        camera.aspect = canvas.clientWidth / canvas.clientHeight;
        camera.updateProjectionMatrix();
      }
      if (hasControlsUpdated) {
        renderer.render(scene, camera);
      } else {
        renderer.render(scene, camera);
      }
      requestAnimationFrame(inner);
    };
  }
}
